// YourComponent.stories.js

import React from 'react';
import '../components/styles.css'

import VotingCard from '../components/VotingCard';

const options = [
  {
    text: "Sim",
    votes: 5
  },
  {
    text: "N√£o",
    votes: 2
  },
  {
    text: "Talvez",
    votes: 6
  }
]

function increaseChoice(index){
  options[index].votes++;
}

//üëá This default export determines where your story goes in the story list
export default {
  title: 'VotingCard',
  component: VotingCard,
};

//üëá We create a ‚Äútemplate‚Äù of how args map to rendering
const Template = (args) => <VotingCard {...args} />;

export const FirstStory = Template.bind({});;
export const SecondStory = Template.bind({});

FirstStory.storyName = "Segue o relator";
FirstStory.args = {
  title: "Segue o relator?",
  state: 'open',
  options: options.slice(0, 2),
  onChose: increaseChoice
};

SecondStory.storyName = "Continuamos amanh√£";
SecondStory.args = {
  title: "Continuamos amanh√£?",
  state: 'closed',
  options: options.slice(0, 3),
  onChose: increaseChoice
};


FirstStory.parameters = { storySource: { source: "(args) => <VotingCard {...args} />" }, ...FirstStory.parameters };
SecondStory.parameters = { storySource: { source: "(args) => <VotingCard {...args} />" }, ...SecondStory.parameters };